import React from 'react';
import './App.css';
import qs from 'qs';
import ReactPlayer, {YouTubeConfig} from 'react-player/youtube';
import * as QueryString from "qs";


function App() {
    let split = window.location.href.split('?');
    console.log(window.location.href);
    // const url: string = searchParams.get('v') != undefined ? 'https://www.youtube.com/watch?v=' + searchParams.get('v') : 'https://www.youtube.com/watch?v=issm76gtOvc';
    let v: string | string[] | QueryString.ParsedQs | QueryString.ParsedQs[] | undefined = "";
    if (split.length > 1) {
        v = qs.parse(split[1]).v;
        console.log(v);
    }
    let url;
    if (v == undefined || v.length == 0) {
        url = 'https://www.youtube.com/watch?v=issm76gtOvc';
    } else {
        url = 'https://www.youtube.com/watch?v=' + v;
    }
    // console.log(searchParams.get('v'));
    console.log(url);
    // const onPlayerReady: YouTubeProps['onReady'] = (event) => {
    //     // access to player in all event handlers via event.target
    //     event.target.pauseVideo();
    // }
    //
    // const opts: YouTubeProps['opts'] = {
    //     height: '390',
    //     width: '640',
    //     playerVars: {
    //         // https://developers.google.com/youtube/player_parameters
    //         autoplay: 1,
    //     },
    // };

    const config: YouTubeConfig = {
        playerVars: {
            autoplay: 1,
            controls: 1
        },
    }

    return (
        <div className="App">
            <header className="App-header">
                <ReactPlayer url={url} config={config}/>
                {/*<YouTube videoId="2g811Eo7K8U" opts={opts} onReady={onPlayerReady}/>;*/}
            </header>
        </div>);
}

export default App;
